.app-wrapper{
  width: 1200px;
  margin: 0 auto;
  display: grid;
  grid-template-areas:
          "h h"
          "n c";
  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
}


.app-wrpper-comtent{
  grid-area: c;
  position: relative;
  min-height: calc(100vh - 100px);
}